.sec3{
    display: flex;
    flex-wrap: wrap;
    width: 86vw;
    margin-top: 2.5vw;
    margin-bottom: 2.5vw;
    justify-content: center;
    min-width: 270px;
    justify-content: center;
    align-items: center;
    background-color:  #ebdab5;
    .container1{
        display: flex;
        flex-direction: column;
        padding: 1.5vw;
        width: 35vw;
        min-width: 250px;
     
    }
    .container2{
        display: flex;
        flex-direction: column;
        padding: 2.5vw;
        width: 35vw;
        margin-top: 10px;
        min-width: 250px;
        align-items: center;
        justify-content: center;
        .form{
            .input{
                width: 35vw;
                min-width: 250px;
                border: 1px solid #51c37e;
                font-size: max(12px , 1.2vw);
                border-bottom: 3px solid #51c37e;
            }
            .checkbox{
                font-size: max(12px , 1.2vw);
            }
            .btn{
                display: flex;
                font-size: 14px;
                font-weight: bold;
                background-color: #51c37e;
                color: white;
                width: 125px;
                height: 30px;
                border-radius: 3px;
                align-items: center;
                justify-content: center;
                margin-top: 20px;
                margin-bottom: 20px;

                &:hover{
                    border: 2px solid #51c37e ;
                }
            }
            
        }
    }
}