.sec1{
  display: flex;
  flex-wrap: wrap;
  width: 86vw;
  justify-content: center;
  align-items: center;
  .container1{
    display: flex; 
    flex-direction: column;
    padding: 1.5vw;
    width: 40vw;
    min-width:250px;
    margin-top: 10px; 
    align-items: center;
    justify-content: center;
    
        .topimg{
            display: flex;
            align-items: center;
            justify-content: center;
            width: 40vw;
            height: 40vw;
            min-width: 250px;
            min-height: 250px;
            background-color:#f5f3f0;
          img{
            display: flex;
            width: 25vw;
            min-width: 186px;
            min-height: 186px;
            height: 25vw;
            border-radius: 20px;
          }
        }

        .container1sub{
            display: flex;
            justify-content: space-between;
            width: 40vw;
            min-width:250px;
        }

        .bottomimg{
            display: flex;
            width: 12vw;
            min-width: 80px;
            min-height: 80px;
            height: 12vw;
            align-items: center;
            justify-content: center;
            margin: .5vw .25vw;
            border: 1px solid #3e3e3e,;

            &:hover{
              border: 2px solid #51c37e ;
          }

            img{
            display: flex;
            width: 10vw;
            height: 10vw;
            min-width: 67px;
            min-height: 67px;
            border-radius: 20px;
             

            }
          
        }
        
      }
      .container2{
        display: flex;
        width: 40vw;
        min-width: 250px;
        padding: 1.5vw;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: center;
         
        
        .btn{
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 16px;
          font-weight: bold;
          border-radius: 3px;
          margin-top:30px;
          width: 150px;
          height: 40px;
          background-color: #51c37e ;
          color: white;

          &:hover{
            border: 2px solid #51c37e ;
        }
        }
      }
}